const rAF = (function(){
  return window.requestAnimationFrame ||
         window.webkitRequestAnimationFrame ||
         window.mozRequestAnimationFrame ||
         function (callback) {
           setTimeout(callback, 1000 / 60);
         }
})();

/**
 * 
 * @param {Number} t 动画已经执行的时间
 * @param {Number} b 起始位置
 * @param {Number} c 终止位置
 * @param {Number} d 动画执行时间
 */

const Quad = {
  easeIn: function (t, b, c, d) {
    return c * (t /= d )* t + b;
  },
  easeOut: function(t,b,c,d){
    return -c *(t/=d)*(t-2) + b;
  },
  easeInOut: function(t,b,c,d){
    if((t/=d/2) < 1) return c/2*t*t + b;
    return -c/2 * ((--t)*(t-2) - 1) + b;
  },
}

let _rafScroll = (origin, target, cb, startTime, time, easing) => {
  let _time = new Date().getTime() - startTime;
  let _currentStatus = !stopScrollStatus
  if(_currentStatus && _time <= time) {
    let _currentScrollTop = Math.round(easing(_time, origin, target - origin, time));
    document.documentElement.scrollTop = document.body.scrollTop = _currentScrollTop;
    rAF(()=>{
      _rafScroll(origin, target, cb, startTime, time, easing);
    })
  }else if(!_currentStatus){
    stopScrollStatus = false
    cb(false);
  }else{
    document.documentElement.scrollTop = document.body.scrollTop = target;
    cb(true);
  }
}

let stopScrollStatus;
// 平滑滚动
const smoothScroll = (target, cb, time, easing) => {
  let origin = Math.round(document.documentElement.scrollTop || document.body.scrollTop);
  let startTime = new Date().getTime();
  let duration = time || 2000;
  requestAnimationFrame(()=>{
    stopScrollStatus = false
    _rafScroll(origin, target, cb, startTime, duration, easing);
  })
}

const throttle = function (func, delay) {
  let prev = Date.now();
  return function(){
    let context = this;
    let args = arguments;
    let now = Date.now();
    if(now - prev >= delay) {
      func.apply(context, args);
      prev = Date.now();
    }
  }
}

function debounce(fn,delay){
  let timer = null
  return function() {
      if(timer){
          clearTimeout(timer) 
      }
      timer = setTimeout(fn,delay)
  }
}

// 是否在屏幕里
const isInScreen = function (dom, adjustHeight = 0) {
  let screenHeight = window.innerHeight
  let domBoundingRect = dom && dom.getBoundingClientRect().top + adjustHeight
  if(domBoundingRect && domBoundingRect < screenHeight){
    return true
  }
  return false
}

const listenFade = function (className) {
  let fadeTrottle = throttle(function(){
    let fades = document.querySelectorAll(className);
    fades.forEach(fade => {
      if(isInScreen(fade)) {
        fade.classList.add('faded')
      }
    })
  }, 500)
  let fadeDebounce = debounce(function(){
    let fades = document.querySelectorAll(className);
    fades.forEach(fade => {
      if(isInScreen(fade)) {
        fade.classList.add('faded')
      }
    })
  },500)

  setTimeout(()=>{
    fadeDebounce();
  }, 700)

  window.addEventListener('scroll', function(){
    fadeTrottle();
    fadeDebounce();
  })
}

module.exports = {
  smoothScroll,
  debounce,
  throttle,
  isInScreen,
  listenFade,
  rAF,
  Quad
}