<template>
  <div class="header" :style="`background-color: rgba(13,13,70,${ headerBgOpacity });`">
    <a href="/" class="logo"><img src="../assets/header-logo.svg" alt="logo"></a>
    <ul class="menu">
      <li v-if="$i18n.locale === 'zh'" @click="goToFloor('.service-floor')"><a href="javascript:void(0)">{{ $t("header.service") }}</a></li>
      <li v-if="$i18n.locale === 'zh'" @click="goToFloor('.team-floor')"><a href="javascript:void(0)">{{ $t("header.team") }}</a></li>
      <!-- <li @click="goToFloor('.join-floor')"><a href="javascript:void(0)">{{ $t("header.culture") }}</a></li> -->
      <li v-if="$i18n.locale === 'zh'" @click="goToFloor('.join-floor')"><a href="javascript:void(0)" @click="goToFloor('.join-floor')">{{ $t("header.recruitment") }}</a></li>
      <li @click="changeLanguage">
        <a href="javascript:void(0)">{{ $i18n.locale === 'zh' ? 'EN' : '中文' }}</a>
      </li>
    </ul>
  </div>
</template>
<script>
import { smoothScroll, Quad } from '../utils/index.js'
export default {
  data() {
    return {
      headerBgOpacity: 0,
      language: 'zh'
    }
  },
  mounted(){
    this.setHeaderBg();
    window.addEventListener('scroll',this.handleScroll)
  },
  methods: {
    handleScroll(){
      this.setHeaderBg();
    },
    setHeaderBg(){
      let displayHeight = 500;
      let startOffset = 300;
      let maxOpacity = .8;
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if(scrollTop >= startOffset && scrollTop < displayHeight + startOffset && this.headerBgOpacity !== undefined){
        this.headerBgOpacity = ( scrollTop - startOffset ) / displayHeight * maxOpacity;
      }else if(scrollTop < startOffset){
        this.headerBgOpacity = 0
      }else if(scrollTop >= displayHeight + startOffset){
        this.headerBgOpacity = maxOpacity
      }

    },
    goToFloor(floor) {
      let targetDom = document.querySelector(floor);
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if(targetDom && targetDom.getBoundingClientRect) {
        let target = targetDom.getBoundingClientRect().top + scrollTop;
        smoothScroll(target, function() {
        }, 600, Quad.easeInOut)
      }
    },
    changeLanguage(){
      let path = this.$route.path;
      console.log(path)
      if(/\/zh\//.test(path)) {
        path = path.replace('/zh/', '/')
      }else{
        path = '/zh' + path
      }
      this.$router.push({
        path
      })
    }
  }
}
</script>
<style lang="less">
.header{
  width: 100%;
  height: 90px;
  display: flex;
  padding: 0 40px;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  transition: background-color 0s linear;
}

.logo{
  width: 65px;
  margin-left: 5%;
  
  img{
    width: 100%;
    display: block;
  }
}

.menu{
  display: flex;
  li{
    a{
      font-size: 16px;
      padding: 10px 20px;
      display: inline;
      color: #fff;
      transition: all 0.3s;
    }

    &:hover{
      a{
        color: #6DDFF5;
      }
    }
  }
}

@media (max-width: 1000px) {
  .menu{
    
    li:nth-child(-n+3){
      display: none;
    }

    li {
      a{
        font-size: 14px;
      }
    }
  }

  .header{
    height: 50px;
    padding: 0;
  }

  .logo{
    width: 36px;
    left: 20px;
  }
}

@media (max-width: 1200px) and (min-width: 1000px) {
  .menu{
    li{
      a{
        font-size: 14px;
      }
    }
  }
}
</style>