<template>
  <div :class="`register-box${show ? ' register-box--active' : ''}`">
    <div class="register-box-bgLayer" @click="close"></div>
    <div class="register-box-main">
      <div class="register-box-form">
        <div class="rb-form-title">
          {{ $t('register.title') }}
        </div>
        <div class="rb-form-input">
          <input :disabled="status!==''" type="text" name="firstname" :placeholder="$t('register.firstname')" v-model="firstname">
        </div>
        <div class="rb-form-input">
          <input :disabled="status!==''" type="text" name="lastname" :placeholder="$t('register.lastname')" v-model="lastname">
        </div>
        <div class="rb-form-input">
          <input :disabled="status!==''" type="text" name="phone" :placeholder="$t('register.phone')" v-model="phone">
        </div>
        <div class="rb-form-input">
          <input :disabled="status!==''" type="text" name="email" :placeholder="$t('register.email')" v-model="email">
          <span :class="`email-status${emailValid ? ' checked' : email ? ' error' : ''}`"></span>
        </div>
        <div class="rb-form-submit" @click="submit">
          <span v-if="!status">{{ $t('register.apply') }}</span>
          <img v-if="status==='loading'" class="loading-picture" src="../assets/loading.svg" alt="">
          <span v-if="status==='success'">{{ $t('register.submit') }}</span>
        </div>
        <div class="rb-form-cancel" @click="close"></div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  props: {
    show: Boolean
  },
  data(){
    return {
      firstname: '',
      lastname: '',
      phone: '',
      email: '',
      status: ''
    }
  },
  methods:{
    close(){
      if(this.status) {
        this.status = '';
        this.firstname = '';
        this.lastname = '';
        this.phone = '';
        this.email = '';
      }
      this.$emit('close')
    },
    submit(){
      this.status = 'loading';
      let firstname = this.firstname;
      let lastname = this.lastname;
      let phone = this.phone;
      let email = this.email;
      axios({
        url: '/api/register',
        method: 'post',
        data: {
          firstname,
          lastname,
          phone,
          email
        }
      }).then(res=>{
        if(res.data.status === 200) {
          setTimeout(()=>{
            this.status = 'success'
          },1000)
        }
      })
    }
  },
  computed:{
    emailValid(){
      if(/^[.A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(this.email)){
        return true
      }
      return false
    }
  }
}
</script>

