import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../pages/home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/zh',
    name: '首页',
    component: Home,
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router