<template>
  <div id="app" :class="`lang-${$i18n.locale}`">
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'App',
  watch: {
    $route(from,to){
      if(/\/zh\//.test(to.path)) {
        this.$i18n.locale = 'en'
      }else{
        this.$i18n.locale = 'zh'
      }
    }
  }
}
</script>