<template>
  <div class="main-content">
    <Header />
    <Banner @clickRegister="clickRegister" />
    <Service v-if="$i18n.locale === 'zh'" />
    <Team v-if="$i18n.locale === 'zh'" />
    <Join v-if="$i18n.locale === 'zh'" />
    <Footer v-if="$i18n.locale === 'zh'" />
    <FooterEN v-if="$i18n.locale === 'en'" />
    <RegisterBox @close="registerBoxHide" :show="registerBoxShow" />
  </div>
</template>

<script>
import Header from '../components/Header'
import Banner from '../components/Banner.vue'
import Service from '../components/Service.vue'
import Team from '../components/Team.vue'
import Join from '../components/Join.vue'
import Footer from '../components/Footer.vue'
import FooterEN from '../components/FooterEN.vue'
import RegisterBox from '../components/RigisterBox.vue'
import { listenFade } from '../utils/index.js'
export default {
  name: 'App',
  data(){
    return {
      registerBoxShow: false
    }
  },
  components: {
    Header,
    Banner,
    Service,
    Team,
    Join,
    Footer,
    FooterEN,
    RegisterBox
  },
  mounted(){
    listenFade('.fade')
  },
  methods: {
    clickRegister(){
      this.registerBoxShow = true
    },
    registerBoxHide(){
      this.registerBoxShow = false
    }
  }
}
</script>